import React from 'react';
import { graphql, Link } from "gatsby";
import { Helmet } from "react-helmet"
import Layout from "../layouts/Layout";
import * as Markdown from 'react-markdown';
import PortfolioDetailImages from "../components/Portfolio/presentation/PortfolioDetailImages";
import Contact from '../components/Contact';

const PortfolioDetail = props => {
    const entry = props.data.contentfulPortfolioDetail;

    const {
        entryClient,
        entryHeroImage,
        entryHeadline,
        entrySubHeadline,
        entryCategory,
        entryDetails,
        entryImages,
    } = entry;

    const categories = entryCategory.split(',')
        .map((category, i) => {
            return (
                <span key={i} className="uk-label uk-margin-small-right uk-margin-small-top">
                    {category}
                </span>
            )
        })

    return (
        <Layout>
            <Helmet>
                <title>{entryClient}</title>
                <script async src="//cdn.embedly.com/widgets/platform.js" charset="UTF-8"></script>
            </Helmet>
            <div className="uk-cover-container">
                <canvas width="1800" height="600"></canvas>
                <img
                    src={entryHeroImage.file.url}
                    alt={entryHeroImage.title}
                    data-uk-img
                    data-uk-cover
                />
            </div>
            <hr className="uk-margin-remove" />
            <div style={{opacity: "0.95", zIndex: "2"}} className="uk-background-muted uk-padding-small uk-margin-large-bottom">
                <div className="uk-container">
                    <ul className="uk-breadcrumb uk-text-uppercase uk-text-small">
                        <li>
                            <Link to={`portfolio/`}>
                                All Case Studies
                            </Link>
                        </li>
                        <li><span>{entryClient}</span></li>
                    </ul>
                </div>
            </div>
            <div className="portfolio-detail portfolio-header uk-container uk-container-small uk-margin">
                <h2 className="content-title uk-margin-large-left@s uk-margin-large-right@s">
                    {entryHeadline}
                </h2>
                <p className="content-lead uk-text-lead uk-margin-large-left@s uk-margin-large-right@s">
                    {entrySubHeadline.entrySubHeadline}
                </p>
                {categories}
            </div>
            <div className="portfolio-detail uk-container uk-container-small uk-margin-large">
                <div className="uk-grid-collapse uk-width-1-1" data-uk-grid>
                    <div className="uk-width-3-5@m uk-margin-right@s">
                        <PortfolioDetailImages entryImages={entryImages} />
                    </div>
                    <div className="post-content uk-width-expand">
                        <Markdown escapeHtml={false} source={entryDetails.entryDetails} />
                    </div>
                </div>
            </div>
            <Contact />
        </Layout>
    );
}

export default PortfolioDetail;
export const portfolioDetailQuery = graphql`
    query portfolioDetailQuery($entrySlug: String!) {
        contentfulPortfolioDetail(entrySlug: {eq: $entrySlug}) {
            entryCategory
            entryClient
            entryDescription
            entryDetails {
                entryDetails
            }
            entryHeadline
            entryHeroImage {
                file {
                    fileName
                    url
                }
            }
            entryImages {
                file {
                    fileName
                    url
                }
                description
            }
            entrySlug
            entrySubHeadline {
                entrySubHeadline
            }
            entryTitle
            entryTopic
        }
    }
`
